import React from 'react';
import down from '../../../img/down.svg';
import circle from '../../../img/circle.svg';
import edit from '../../../img/edit.svg';

import hash from '../../../img/hash.svg';
import lockwite from '../../../img/lock-white.svg';
import downarrow from '../../../img/down-arrow.svg';
import plus from '../../../img/plus.svg';

import thread from '../../../img/threads.svg';
import people from '../../../img/people.svg';
import draft from '../../../img/draft.svg';
import app from '../../../img/app.svg';

const Sidebar = () => {
    return (
        <div className="wrap-channel-sidebar">
            <div className="sidebar-nick-name"> 
                <div className="nick-name">
                    <button>
                        <span>
                            Ais&nbsp;
                            <img  src={down} alt="logo"/>
                        </span>
                    </button>
                    <div>
                        <span>
                            <img  src={circle} alt="logo"/>
                            &nbsp;
                            Alexey
                            &nbsp;
                            <img  src={"https://slack-imgs.com/?c=1&o1=gu&url=https%3A%2F%2Fa.slack-edge.com%2Fproduction-standard-emoji-assets%2F10.2%2Fgoogle-small%2F1f4ac.png"} alt="logo"/>
                        </span>
                    </div>
                </div>
                <button className="sidebar-button-edit-nick-name">
                    <img  src={edit} alt="logo"/>
                </button>
            </div>    
            <button className="button-sidebar-check-task">
                <span className="icon-chennel">
                    <img  src={thread} alt="logo"/>&nbsp;
                    Threads
                </span>
            </button>
            <button className="button-sidebar-check-task">
                <span className="icon-chennel">
                    <img  src={draft} alt="logo"/>&nbsp;
                    Drafts
                </span>
            </button>
            <button className="button-sidebar-check-task">
                <span className="icon-chennel">
                    <img  src={people} alt="logo"/>&nbsp;
                    People
                </span>
            </button>
            <button className="button-sidebar-check-task">
                <span className="icon-chennel">
                    <img  src={app} alt="logo"/>&nbsp;
                    Apps
                </span>
            </button>
            <div className="button-sidebar-check-task-channels">
                <div>
                    <img  src={downarrow} alt="logo"/>&nbsp;
                    Channels 
                    <button>
                        <img  src={plus} alt="logo"/>
                    </button>
                </div>
            </div>     
            <button className="button-sidebar-check-task">
                <span>
                    <img  src={hash} alt="logo"/>&nbsp;
                    general
                </span>
            </button>
            <button className="button-sidebar-check-task">
                <span>
                    <img  src={hash} alt="logo"/>&nbsp;
                    question 
                </span>
            </button>    
            <button className="button-sidebar-check-task">
                <span>
                    <img  src={hash} alt="logo"/>&nbsp;
                    random 
                </span>
            </button>    
            <button className="button-sidebar-check-task">
                <span>
                    <img  src={lockwite} alt="logo"/>&nbsp;
                    task1 
                </span>
            </button>    
            <button className="button-sidebar-check-task">
                <span>
                    <img  src={lockwite} alt="logo"/>&nbsp;
                    task2 
                </span>
            </button>
        </div>
    )
}

export default Sidebar;