import React from 'react';
import lockblack from '../../../img/lock.svg'
import close from '../../../img/close.svg'
import user from '../../../img/user.svg'



const MessangeView = () => {
    return (
        <div className="wrap-view-messange">
            <div className="sidebar-nick-name-view-mes"> 
                <div className="thread-view">
                    <button>
                        <span>
                            Thread&nbsp;
                        </span>
                    </button>
                    <div>
                        <span>
                            <img  src={lockblack} alt="logo"/>
                            &nbsp;
                            task2
                        </span>
                    </div>
                </div>
                <button className="sidebar-button-close-mes">
                    <img  src={close} alt="logo"/>
                </button>
            </div> 
            <div className="view-this-message">
                <div className="user-this-message">
                    <button>
                        <img  src={user} alt="logo"/>
                    </button>
                </div>
                <div className="thisuser-message">
                    <div className="nick-name-user-date">
                        <span>Alexandr</span>
                        <span>Jul 17th at 4:35 PM</span>
                    </div>
                    <div className="text-message">
                        <span>
                        Will only those who accepted 2 tasks have access to the 3rd task? I just threw it off 11 days ago, I would like to get the 3rd task pls
                        </span>
                    </div>
                </div>
            </div>
            <div className="message-line-replies">
                <span>3 replies</span><hr/>
            </div>
            <div className="view-this-message">
                <div className="user-this-message">
                    <button>
                        <img  src={user} alt="logo"/>
                    </button>
                </div>
                <div className="thisuser-message">
                    <div className="nick-name-user-date">
                        <span>ilya.mokin.ais</span>
                        <span>1 days ago</span>
                    </div>
                    <div className="text-message">
                        <span>
                        please check a review also under your messanger also in in github repository
                        </span>
                    </div>
                </div>
            </div>
            <div className="view-this-message">
                <div className="user-this-message">
                    <button>
                        <img  src={user} alt="logo"/>
                    </button>
                </div>
                <div className="thisuser-message">
                    <div className="nick-name-user-date">
                        <span>Alexandr</span>
                        <span>1 days ago</span>
                    </div>
                    <div className="text-message">
                        <span>
                        I fixed my html / css after issue but pr is unanswered
                        </span>
                    </div>
                </div>
            </div>
            <div className="view-this-message">
                <div className="user-this-message">
                    <button>
                        <img  src={user} alt="logo"/>
                    </button>
                </div>
                <div className="thisuser-message">
                    <div className="nick-name-user-date">
                        <span>Alexandr</span>
                        <span>1 days ago</span>
                    </div>
                    <div className="text-message">
                        <span>
                        and re-fill the form after this
                        </span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MessangeView;